'use client';

import { ActionIcon, useMantineColorScheme, Group } from '@mantine/core';
import { IconSun, IconMoon } from '@tabler/icons-react';
import classes from './ThemeToggle.module.css';

export function ThemeToggle() {
    const { setColorScheme, toggleColorScheme } = useMantineColorScheme();

    return (
        <Group justify="center">
            <ActionIcon
                onClick={() => toggleColorScheme()}
                variant="default"
                size="lg"
                radius="xl"
                aria-label="Toggle color scheme"
            >
                <IconSun size={18} className={classes.light} />
                <IconMoon size={18} className={classes.dark} />
            </ActionIcon>
        </Group>
    );
}
